.header {
  width: 100vw;
  height: 10%;
  display: inline-block;
  flex-direction: row;
  justify-content: space-between;
  position: relative;
  background-color: #bee7b8;
}

.nav {
  width: 100%;
  height: 120%;
  display: flex;
  overflow: hidden;
  justify-content: space-around;
  align-items: center;
  position: relative;
  flex-wrap: wrap;
}

.links {
  width: 55%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  overflow: scroll;
  a {
    padding: 30px;
    color: black;
    display: inline-block;
    padding: 10px 20px;
    position: relative;
  
    color: black;
    font-size: 18px;
    text-align: center;
    text-decoration: none;
  }
}

.link:hover:after { 
  width: 100%; 
  left: 0; 
}

.active{
  color: black;
  font-weight: bold;
  pointer-events: none;
}
  
.nav {
  height: 8vh;
  display: flex;
  align-items: center;
  position: -webkit-sticky;
  position: sticky;
  top: -5px;
  z-index: 10;
  width: calc(100%);
  background-color: #bee7b8;
  box-shadow: 0 0.125rem 0.25rem 0 rgb(0 0 0 / 11%);
}

.nav_sections {
  display: inline-flex;
  flex-flow: row nowrap;
  padding-left: 50px;
  flex: 1;
}

.active {
  font-weight: bold;
}

ul {
  display: flex;
  gap: 40px;
  font-size: 0.9rem;
  list-style: none;
  padding: 0;
}

li {
  cursor: pointer;
  padding: 30px;
  color: black;
  display: inline-block;
  padding: 10px 20px;
  position: relative;

  color: black;
  font-size: 18px;
  text-align: center;
  text-decoration: none;
  &:hover {
    text-decoration: underline;
    text-underline-offset: 5px;
    &:after {
      width: 100%; 
      left: 0; 
    }
  }
}

.footer {
  float: right;
  margin: 0 10vw 3vh 0;
  color: grey;
  
}
