.sectionAbout {
    width: 80vw;
    margin-left: calc(20vw - 10px);
    height: 100%;
    display: inline-block;
    flex-direction: row;
    padding-left: 10px;
    margin-right: 0;
    cursor: default;
}

.pfp {
    width: max(20vw, 200px);
    border-radius: 200px;
    padding: 10px;
    box-shadow: 0 0 0 2px #ffc4c4, 0 0 0 5px white, 0 0 0 10px #ffc4c4;
}

.about_showcase {
    width: calc(80% - 20px);
    height: 30vh;
}

.about_intro {
    display: inline; /*inline-flex sidebyside*/
    justify-content: center;
}

.about_description {
    width: 45vw;
    font-size: max(1.2vw, 18px);
    display: inline-flex;
    text-align: center;
    font-family: 'Lato', sans-serif;
}

.contact_icons {
    display: block;
    margin-top: 10px;
}

.icon {
    margin-top: 10px;
    border: 1.5px solid #ffc4c4;
    border-radius: 50%;
    width: 35px;
    padding: 0px 1px 1px 0px;
    &:hover {
        box-shadow: 0 0 8px #ffc4c4;
        border-radius: 50%;
    }
}